.success {
  text-align: center;
}
.success h2 {
  color: seagreen;
}
.success a {
  display: block;
}

.checkout-form {
  width: 400px;
  padding: 2em;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0 1px 5px 1px rgba(0, 0, 0, 0.15);
}
.checkout-form p {
  text-align: center;
}
.checkout-form label {
  min-width: 500px;
  color: #6b7c93;
  font-weight: 300;
  letter-spacing: 0.025em;
}
.checkout-form input,
.checkout-form .StripeElement {
  display: block;
  margin: 10px 0 20px 0;
  padding: 10px 14px;
  font-size: 1em;
  font-family: "Source Code Pro", monospace;
  box-shadow: rgba(50, 50, 93, 0.14902) 0px 1px 3px, rgba(0, 0, 0, 0.0196078) 0px 1px 0px;
  border: 0;
  outline: 0;
  border-radius: 4px;
  background: white;
}
.checkout-form input::placeholder,
.checkout-form .StripeElement::placeholder {
  color: #aab7c4;
}
.checkout-form input:focus,
.checkout-form input .StripeElement--focus,
.checkout-form .StripeElement:focus,
.checkout-form .StripeElement .StripeElement--focus {
  box-shadow: rgba(50, 50, 93, 0.109804) 0px 4px 6px, rgba(0, 0, 0, 0.0784314) 0px 1px 3px;
  -webkit-transition: all 150ms ease;
  transition: all 150ms ease;
}
.checkout-form .order-button {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  text-decoration: none;
  margin-top: 5%;
  background-color: #008cdd;
  border: 1px solid #008cdd;
  font-size: 1em;
  font-weight: bold;
  color: white;
  border-radius: 5px;
  padding: 0.5em 1em;
  width: 200px;
  transition: opacity 200ms;
  cursor: pointer;
}
.checkout-form .order-button.success {
  background-color: seagreen;
  border-color: seagreen;
}
.checkout-form .order-button:hover {
  opacity: 0.8;
}
.checkout-form .order-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.checkout-form a {
  display: block;
  text-align: center;
  margin-top: 1em;
}